<template>
  <div id="app">
    <v-container>
      <v-layout row>
        <v-card style="margin: 30px auto; width: 500px;">
          <v-card-title primary-title>
            <h4>Registrar</h4>
          </v-card-title>
          <v-form style="margin: 15px">
            <v-text-field
              prepend-icon="mdi-phone"
              name="Phone"
              label="Telefono"
              v-model="phone"
            ></v-text-field>
            <v-text-field
              prepend-icon="mdi-lock"
              name="Password"
              label="Contraseña"
              type="password"
              v-model="password"
            ></v-text-field>
            <v-card-actions>
              <v-btn @click="register()" color="primary" large block>Register</v-btn>
            </v-card-actions>
            <br />
            <span
              >Tengo una cuenta, deseo hacer <router-link to="/">login</router-link></span>
          </v-form>
        </v-card>
      </v-layout>
        <v-alert v-if="error" border="top" color="red lighten-2" dark>
          {{error_message}}
        </v-alert>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Register",

  data: () => ({
    server: "http://localhost:9292",
    phone: "",
    password: "",
    error: false,
    error_message: 'Ocurrio un error'
  }),

  methods: {
    register() {
      axios({
        method: "POST",
        url: this.server + "/api/v1/users/register",
        data: { phone: this.phone, password: this.password },
      }).then(
        (response) => {
          console.log(response);
          if (response.data.status == 'success') {
            this.$router.push("/");
          }else{
            this.error = true;
            if (response.data.status == 'fail') {
              this.error_message = response.data.message;
            }
          }
        },
        (response) => {
          this.error = true;
          console.log(response);
        }
      );
    },
  },
};
</script>
